.bearcard-container {
    width: 160px;
    border-radius: 4px;
    box-shadow: 0 0 6px 0 rgba(160, 88, 88, 0.15);
    overflow: hidden;
}

.bearcard {
  height: 200px;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  position: relative;
}

.bearcard-name {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 10px;

    text-align: center;
    padding: 4px;
    margin: 0;
    background-color: rgba(0,0,0,.60);
    color: white;
}

.bearcard-weight {
    position: absolute;
    top: 8px;
    right: 8px;
    margin: 0;
    
    display: inline;
    padding: 4px;
    font-weight: bold;
    background-color: rgba(245, 245, 245, 0.6);
    color: rgb(233, 53, 53);
}

.bearcard-actions {
    background-color: white;
    display: flex;
}

.bearcard-actions > div {
    flex: 1;
    padding: 4px;
    text-align: center;
    font-size: .8em;
    cursor: pointer;
    transition: background-color .2s ease-in;
}

.bearcard-actions > div:hover {
    background-color: rgb(255, 220, 154);
}
